
console.log('Welcome');

const colors = [
    '#FFFFFF',
    '#2196F3',
    '#4CAF50',
    '#FF9800',
    '#009688',
    '#795548',
  ];

//   const refs = {
const start = document.querySelector('button[data-action="start"]');
     const stop = document.querySelector('button[data-action="stop"]');
     const body = document.querySelector('body');
//   };

console.log(start);

  start.addEventListener('click', onColorSwitchStart);
  stop.addEventListener('click', onColorSwitchStop);
stop.disabled = true;

  const randomIntegerFromInterval = (min, max) => {
    return Math.floor(Math.random() * (max - min + 1) + min);
  };


  function bgdSwitch() {
      return body.style.backgroundColor = colors[randomIntegerFromInterval(0, colors.length - 1)]
  };

  function onColorSwitchStart() {
      backgroundInterval = setInterval(bgdSwitch, 1000);

      stop.disabled = false;
      start.disabled = true;
  };

  function onColorSwitchStop() {
clearInterval(backgroundInterval);
stop.disabled = true;
start.disabled = false;
  };


